.shopping-page-cart-container{
    min-height: 600px;
    width: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr;
}

.shopping-page-cart-receipt {
    padding: 30px 0;
    background-color: lightgreen;
}

.shopping-page-cart-total-price {
    padding: 20px 0;
}

.shopping-page-cart-payment {
    padding: 30px 0;
    background-color: magenta;
    display: flex;
    flex-direction: column;
    align-items: center;
    /*justify-content: center;*/
}

.shopping-page-cart-payment-title {
    margin-bottom: 20px;
    font-size: 30px;
}

.payment-methods {
    margin-bottom: 30px;
    display: flex;
    justify-content: center;
}

.payment-method {
    position: relative;
    width: 100px;
    height: 100px;
    background-color: #fff;
    cursor: pointer;
}

.selected-payment-method {
    box-sizing: border-box;
    border-bottom: 6px solid limegreen;
    transition: 200ms;
}

.payment-method-overlay {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    cursor: not-allowed;
    background-color: rgba(0,0,0,0.3);
}

.place-order-button {
    padding: 10px;
    border-radius: 6px;
    background-color: orange;
    border: 2px solid darkorange;
    box-shadow: 0 0 20px red;
}

.place-order-button-selected {
    box-shadow: 0 0 20px green;
    transition: 300ms;
    cursor: pointer;
}

.place-order-button-selected {
    box-shadow: 0 0 20px green;
    transition: 300ms;
    cursor: pointer;
}

@media only screen and (max-width: 768px) {
    .shopping-page-cart-container{
        grid-template-columns: 1fr;
    }
}